import React from "react";
import ReactDOM from "react-dom";
import cars from "./practice";

const [honda, tesla, scion] = cars;

const {
  model: civicModel,
  coloursByPopularity: [civicTopColour],
  speedStats: { topSpeed: civicTopSpeed }
} = honda;
const {
  model: teslaModel,
  coloursByPopularity: [teslaTopColour],
  speedStats: { topSpeed: teslaTopSpeed }
} = tesla;
const {
  model: scionModel,
  coloursByPopularity: [, , scionTopColour],
  speedStats: { topSpeed: scionTopSpeed }
} = scion;

ReactDOM.render(
  <table>
    <tr>
      <th>Brand</th>
      <th>Top Speed</th>
      <th>Top Color</th>
    </tr>
    <tr>
      <td>{teslaModel}</td>
      <td>{teslaTopSpeed}</td>
      <td>{teslaTopColour}</td>
    </tr>
    <tr>
      <td>{civicModel}</td>
      <td>{civicTopSpeed}</td>
      <td>{civicTopColour}</td>
    </tr>
    <tr>
      <td>{scionModel}</td>
      <td>{scionTopSpeed}</td>
      <td>{scionTopColour}</td>
    </tr>
  </table>,
  document.getElementById("root")
);
